@-webkit-keyframes ripple {

  /* Safari 4.0 - 8.0 */
  0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
    opacity: 0;
  }

  25% {
    opacity: 1;
  }

  100% {
    -webkit-transform: scale(2.5);
    transform: scale(2.5);
    opacity: 0;
  }
}

@keyframes ripple {
  0% {
    transform: scale(0.5);
    opacity: 0;
  }

  25% {
    opacity: 1;
  }

  100% {
    transform: scale(2.5);
    opacity: 0;
  }
}